import React from "react";
import "components/Popup/style.css";

const groupsStyle = {
  color: "grey",
  fontWeight: "light",
  fontSize: "1em",
};

const GroupRenderer = (props) => {
  var any = "";
  var array = [];
  props.selected.forEach((element) => {
    any = props.groups.find((x) => x.id === element);
    array.push(any);
  });

  return (
    <div
      style={{
        backgroundColor: props.bgColour,
        borderRadius: "3px",
        width: "30vw",
      }}
    >
      <table
        style={{ color: props.textColor, margin: "0 auto", width: "100%" }}
      >
        <tbody>
          <tr
            style={{
              textAlign: "left",
              fontSize: "1em",
              fontWeight: "lighter",
            }}
          >
            <th>ID</th>
            <th>Type</th>
          </tr>
          {array.map((item, index) => (
            <tr key={index}>
              <td style={{ fontWeight: "lighter", fontSize: "1em" }}>
                {item.level}
              </td>
              <td style={{ fontWeight: "lighter", fontSize: "1em" }}>
                {item.group_name}
              </td>
              {/* <td
                          style={{ fontWeight: "lighter", fontSize: "1.3em" }}
                        >
                          {item.error}
                        </td> */}
            </tr>
          ))}
        </tbody>
      </table>
    </div>
  );
};

export default GroupRenderer;
