import "bootstrap/dist/css/bootstrap.css";
import React, { Component } from "react";
import BootstrapTable from "react-bootstrap-table-next";
import filterFactory, {
  textFilter,
  dateFilter,
} from "react-bootstrap-table2-filter";
import { withRouter } from "react-router-dom";
import Popup from "components/Popup/Popup.js";
import paginationFactory from "react-bootstrap-table2-paginator";
import moment from "moment";
import axios from "axios";

class GroupsTable extends Component {
  constructor(props) {
    super(props);
    this.state = {
      showPopup: false,
      rowId: null,
      tests: [],
      testsFiltered: [],
      errors: [],
      errorsFiltered: [],
    };
  }

  componentDidMount() {
    axios
      .get("http://" + global.BASE_URL + "/api/tests", {
        headers: {
          "Content-Type": "application/json;charset=UTF-8",
          Authorization: "Bearer " + localStorage.usertoken,
        },
      })
      .then((response) => {
        this.setState({
          tests: response.data,
        });
      });
    axios
      .get("http://" + global.BASE_URL + "/api/errors", {
        headers: {
          "Content-Type": "application/json;charset=UTF-8",
          Authorization: "Bearer " + localStorage.usertoken,
        },
      })
      .then((response) => {
        this.setState({ errors: response.data });
      });
  }

  filterTests = (rowId) => {
    this.setState({
      testsFiltered: this.state.tests.filter((test) => {
        if (test.id === rowId) {
          return test;
        } else return null;
      }),
    });
    this.setState({
      errorsFiltered: this.state.errors.filter((error) => {
        if (error.test_id === rowId) {
          return error;
        } else return null;
      }),
    });
  };

  togglePopup = () => {
    this.setState({
      showPopup: !this.state.showPopup,
    });
  };

  locationFormatter = (column, colIndex, { sortElement, filterElement }) => {
    return (
      <div style={{ display: "flex", flexDirection: "column" }}>
        {filterElement}
        {column.text}
      </div>
    );
  };

  render() {
    const dateFormatter = (cell) => {
      return <span>{moment(cell).format("DD.MM.YYYY k:mm:ss")}</span>;
    };

    const passedFormatter = (cell, row) => {
      if (row.responseok === cell) {
        return (
          <p style={{ color: "green" }}>
            {row.responseok}/{cell}
          </p>
        );
      } else {
        return (
          <p style={{ color: "red" }}>
            {row.responseok}/{cell}
          </p>
        );
      }
    };

    const columns = [
      {
        dataField: "id",
        text: "ID",
        sort: true,
        headerStyle: (colum, colIndex) => {
          return { width: "50px" };
        },
      },
      {
        dataField: "level",
        text: "Levels",
        sort: true,
        filter: textFilter(),
        headerStyle: () => {
          return {
            width: "100px",
          };
        },
        headerFormatter: this.locationFormatter,
        style: {
          whiteSpace: "nowrap",
          textOverflow: "ellipsis",
          overflow: "hidden",
        },
      },
      {
        dataField: "group_name",
        text: "Locations",
        sort: true,
        filter: textFilter(),
        headerStyle: () => {
          return {
            width: "160px",
          };
        },
        headerFormatter: this.locationFormatter,
        style: {
          whiteSpace: "nowrap",
          textOverflow: "ellipsis",
          overflow: "hidden",
        },
      },
      {
        dataField: "lights",
        text: "Pass",
        sort: true,
        formatter: passedFormatter,
        headerStyle: (colum, colIndex) => {
          return { width: "60px" };
        },
      },
      {
        dataField: "errors",
        text: "Warnings",
        sort: true,
        hidden: true,
      },
      {
        dataField: "result",
        text: "Status",
        sort: true,
        filter: textFilter(),
        headerFormatter: this.locationFormatter,
        headerStyle: (colum, colIndex) => {
          return { width: "80px" };
        },
      },
      {
        dataField: "set",
        text: "Set",
        sort: true,
        filter: textFilter(),
        headerFormatter: this.locationFormatter,
        headerStyle: (colum, colIndex) => {
          return { width: "90px" };
        },
        style: {
          whiteSpace: "nowrap",
          textOverflow: "ellipsis",
          overflow: "hidden",
        },
      },
      {
        dataField: "created_at",
        text: "Start Time",
        sort: true,
        filter: dateFilter({ dateStyle: { width: "140px", margin: "0px" } }),
        formatter: dateFormatter,
        headerFormatter: this.locationFormatter,
        headerStyle: (colum, colIndex) => {
          return { width: "140px" };
        },
      },
    ];

    const rowEvents = {
      onClick: (e, row) => {
        this.filterTests(row.id);
        this.togglePopup();
      },
    };

    const options = {
      sizePerPageList: [
        {
          text: "5",
          value: 5,
        },
        {
          text: "10",
          value: 10,
        },
        {
          text: "All",
          value: this.props.tests.length,
        },
      ],
    };

    return (
      <div style={{ marginTop: 20 }}>
        <BootstrapTable
          wrapperClasses="table-responsive"
          bordered={false}
          hover
          keyField="id"
          data={this.props.tests}
          columns={columns}
          filter={filterFactory()}
          rowEvents={rowEvents}
          pagination={paginationFactory(options)}
        />

        <Popup
          showPopup={this.state.showPopup}
          testsFiltered={this.state.testsFiltered}
          errorsFiltered={this.state.errorsFiltered}
          rowId={this.state.rowId}
          text='Click "Close Button" to hide popup'
          closePopup={this.togglePopup.bind(this)}
        />
      </div>
    );
  }
}

export default withRouter(GroupsTable);
