import React from "react";
import { Link } from "react-router-dom";
import Card from "components/Card/Card.js";
import CardBody from "components/Card/CardBody.js";
import GridContainer from "components/Grid/GridContainer.js";
import GridItem from "components/Grid/GridItem.js";
import Icon from "@material-ui/core/Icon";
import LocationCityIcon from "@material-ui/icons/LocationCity";
import background from "assets/img/trianglify-lowres.png";
import jwt_decode from "jwt-decode";

const whiteTxt = {
  color: "white"
};

const delDecor = {
  textDecoration: "none"
};

const lightText = {
  fontWeight: "100",
  textDecoration: "none"
};

const cardStyle = {
  borderRadius: "1px",
  backgroundImage: `url(${background})`,
  backgroundSize: "cover",
  minHeight: "180px"
};

class Welcome extends React.Component {
  constructor() {
    super();
    this.state = {
      first_name: "",
      last_name: "",
      email: "",
      errors: {}
    };
  }

  componentDidMount() {
    const token = localStorage.usertoken;
    const decoded = jwt_decode(token);
    this.setState({
      first_name: decoded.first_name,
      last_name: decoded.last_name,
      email: decoded.email
    });
  }

  render() {
    return (
      <div style={{ textAlign: "center" }}>
        <h1 style={lightText}>Welcome {this.state.first_name}</h1>
        <h3 style={lightText}>
          This is your emergency lighting dashboard. Below you can see test
          history and navigation. Navigate to your site tab to manage your site
          or to Tests tab to schedule new tests.
        </h3>
        <GridContainer justify="center">
          <GridItem xs={12} sm={6} md={4} lg={3}>
            <Link to="/admin/groups" style={delDecor}>
              <Card style={cardStyle}>
                <CardBody style={whiteTxt}>
                  <h1 style={lightText}>Your site</h1>
                  <LocationCityIcon style={{ fontSize: 75 }} />
                </CardBody>
              </Card>
            </Link>
          </GridItem>
          <GridItem xs={12} sm={6} md={4} lg={3}>
            <Link to="/admin/test" style={delDecor}>
              <Card style={cardStyle}>
                <CardBody style={whiteTxt}>
                  <h1 style={lightText}>Test</h1>
                  <Icon style={{ fontSize: 75 }}>content_paste</Icon>
                </CardBody>
              </Card>
            </Link>
          </GridItem>
          <GridItem xs={12}>
            <h3>Latest improvements</h3>
            <p>version 0.1</p>
            <h5>Site management:</h5>
            <p>1. Edit device by clicking on table cell twice</p>
            <p>2. Edit location name with edit button located under a node</p>
            <h5>Test system:</h5>
            <p>1. Response check has been fixed</p>
          </GridItem>
        </GridContainer>
      </div>
    );
  }
}

export default Welcome;
