import React from "react";
import "./style.css";
import axios from "axios";
import Icon from "@material-ui/core/Icon";
import { IconButton } from "@material-ui/core";
import { Link } from "react-router-dom";
import { CSVLink } from "react-csv";
import { PDFDownloadLink } from "@react-pdf/renderer";
import { PdfDocument } from "./PdfTest";
import moment from "moment";
import { Transition } from "react-transition-group";

const duration = 300;

const defaultStyle = {
  transition: `visibility ${duration}ms ease-in-out, opacity ${duration}ms ease-in-out`,
  opacity: 0,
  visibility: "hidden",
};

const transitionStyles = {
  entering: { opacity: 1, visibility: "visible" },
  entered: { opacity: 1, visibility: "visible" },
  exiting: { opacity: 0 },
  exited: { opacity: 0 },
};

class Popup extends React.Component {
  state = {
    reportData: [],
    errorsCsv: [],
  };

  componentDidMount() {
    //errors data for csv report
    axios
      .get(
        "http://" +
          global.BASE_URL +
          "/api/errors/test/csvdata/" +
          this.props.rowId,
        {
          headers: {
            "Content-Type": "application/json;charset=UTF-8",
            Authorization: "Bearer " + localStorage.usertoken,
          },
        }
      )
      .then((response) => {
        this.setState({ errorsCsv: response.data });
      });
  }

  render() {
    const csvReportArray = this.state.errorsCsv;
    const tests = this.props.testsFiltered[0];
    const errors = this.props.errorsFiltered;
    if (tests && errors) {
      var filename = `report${moment(tests.created_at).format(
        "YYYY-MM-DD-kmmss"
      )}.pdf`;

      return (
        <Transition in={this.props.showPopup} timeout={600}>
          {(state) => (
            <div
              className="popup"
              style={{ ...defaultStyle, ...transitionStyles[state] }}
            >
              <div
                className="popupInnerBg"
                style={{ ...defaultStyle, ...transitionStyles[state] }}
              >
                <div
                  className="popupInner"
                  style={{ ...defaultStyle, ...transitionStyles[state] }}
                >
                  <div className="datagrid">
                    <div style={{ overflowX: "auto" }}>
                      <table>
                        <tbody>
                          <tr>
                            <th>ID</th>
                            <th>Levels</th>
                            <th>Tested Lights</th>
                            <th>Status</th>
                            <th>Warnings</th>
                            <th>Set</th>
                            <th>Test Time</th>
                          </tr>
                          <tr>
                            <td>
                              {tests.id}
                              <Link to={"admin/groups"} />
                            </td>
                            <td>{tests.level}</td>
                            <td>{tests.lights}</td>
                            <td>{tests.result}</td>
                            <td>{tests.errors}</td>
                            <td>{tests.set}</td>
                            <td>
                              {moment(tests.created_at).format(
                                "k:mm DD.MM.YYYY"
                              )}
                            </td>
                          </tr>
                        </tbody>
                      </table>
                    </div>
                    <div className="infoContainer">
                      <table>
                        <tbody>
                          <tr style={{ textAlign: "left" }}>
                            <th>ID</th>
                            <th>Type</th>
                            <th>Location</th>
                            <th>Response</th>
                          </tr>
                          {errors.map((item, index) => (
                            <tr key={index}>
                              <td
                                style={{
                                  fontWeight: "lighter",
                                  fontSize: "1.3em",
                                }}
                              >
                                {item.device_id}
                              </td>
                              <td
                                style={{
                                  fontWeight: "lighter",
                                  fontSize: "1.3em",
                                }}
                              >
                                {item.type}
                              </td>
                              <td
                                style={{
                                  fontWeight: "bold",
                                  fontSize: "1.3em",
                                }}
                              >
                                {item.level} level{" "}
                                <div style={{ fontWeight: "lighter" }}>
                                  {item.group_name}
                                </div>
                              </td>
                              <td
                                style={{
                                  fontWeight: "lighter",
                                  fontSize: "1.3em",
                                }}
                              >
                                {item.error}
                              </td>
                              <td>
                                <button>elo</button>
                              </td>
                            </tr>
                          ))}
                        </tbody>
                      </table>
                    </div>
                  </div>
                  {/* <ErrorsRender /> */}
                  <div style={{ marginTop: "20px" }}>
                    <CSVLink
                      data={csvReportArray}
                      style={{
                        textDecoration: "none",
                        margin: "10px",
                        padding: "5px",
                        color: "#4a4a4a",
                        backgroundColor: "#f2f2f2",
                        border: "1px solid #4a4a4a",
                      }}
                    >
                      REPORT
                    </CSVLink>
                    <PDFDownloadLink
                      document={
                        <PdfDocument errorsData={errors} testsData={tests} />
                      }
                      fileName={filename}
                      style={{
                        textDecoration: "none",
                        margin: "10px",
                        padding: "5px",
                        color: "#4a4a4a",
                        backgroundColor: "#f2f2f2",
                        border: "1px solid #4a4a4a",
                      }}
                    >
                      TEST PDF
                    </PDFDownloadLink>
                  </div>
                  <div className="buttonContainer">
                    <IconButton
                      color="secondary"
                      onClick={this.props.closePopup}
                    >
                      <Icon>cancel</Icon>
                    </IconButton>
                  </div>
                </div>
              </div>
            </div>
          )}
        </Transition>
      );
    } else return null;
  }
}

export default Popup;
