  //           key={index}
  //           style={(textWhite, textAlign)}
  //         >
  //           <Button
  //             style={{
  //               borderRadius: "2px",
  //               backgroundImage: `url(${background})`,
  //               backgroundSize: "cover",
  //               minHeight: "50px",
  //               width: "86%",
